/* 
  Definição das variáveis que estão no alc-tabs 
    --bg-color
    --border-color
    --border-width
    --outline-color
*/

.alc-tabs__tab:focus-visible {
  @apply outline outline-2 outline-[--outline-color] -outline-offset-8;
} 

.alc-tabs__tab--content {
  @apply border-[--border-color] p-3 bg-[--bg-color];
  border-width: var(--border-width);
  margin-top: calc(-1 * var(--border-width));
}

